body {
  margin: 0;
  padding: 20px;
  font-family: Verdana, sans-serif;
}

h1 {
  text-align: left;
}

.loader {
  width: 50px;
  height: 50px;
  border: 5px solid #f3f3f3;
  border-top-color: #9370db;
  border-radius: 50%;
  margin: 20px auto;
  display: none;
}

.breed-select {
  border-color: #000;
  padding: 10px;
  font-size: 18px;
  display: block;
}

.cat-info {
  text-align: left;
  margin-top: 20px;
  display: none;
}

.cat-info img, .cat-image {
  width: 200px;
  height: auto;
  margin-bottom: 10px;
}

.error {
  color: red;
  text-align: center;
  margin-top: 20px;
  display: none;
}

